.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: #F8F8F8;
  border-radius: 10px;

  @media (max-width: 1130px) {
    font-size: 14px;
    line-height: 18px;
  }

  &-started, &-submit {
    padding: 20px 46px;
    background-color: var(--primary-color);
    min-width: 208px;
    margin-right: 32px;
    transition: background-color 0.3s ease;

    @media (max-width: 1130px) {
      padding: 16px 35px;
      margin-right: 16px;
      min-width: 160px;
    }

    @include mobile {
      padding: 13px 30px;
      margin-right: 0;
      width: 100%;
    }

    svg {
      margin-left: 6px;
      transition: transform 0.3s ease;
    }

    &:hover {
      background-color: var(--darkPrimary-color);
      svg {
        transform:translateX(15px);
      }
    }

    &:active {
      svg {
        transform:translateX(30px);
      }
    }
  }

  &-started {
    @include mobile {
      margin-bottom: 16px;
    }
  }

  &-learn__more {
    padding: 22px 46px;
    color: var(--primary-color);
    box-shadow: 0px 1px 0px var(--primary-color), 0px -1px 0px var(--primary-color), 1px 0px 0px var(--primary-color), -1px 0px 0px var(--primary-color);
    min-width: 208px;
    transition: background-color 0.3s ease, box-shadow 0.3s ease, color 0.3s ease;

    @media (max-width: 1130px) {
      padding: 18px 35px;
      margin-right: 16px;
      min-width: 180px;
    }

    @include mobile {
      padding: 14px 30px;
      width: 100%;
    }

    &:hover {
      color: var(--light-color);
      background-color: var(--primary-color);
    }
  }

  &-view__all {
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
    padding: 22px 58px;
    color: var(--primary-color);
    box-shadow: 0px 1px 0px var(--primary-color), 0px -1px 0px var(--primary-color), 1px 0px 0px var(--primary-color), -1px 0px 0px var(--primary-color);
    transition: background-color 0.3s ease, box-shadow 0.3s ease, color 0.3s ease;

    &:hover {
      color: var(--light-color);
      background-color: var(--primary-color);
    }

    @media (max-width: 1130px) {
      padding: 18px 35px;
      min-width: 180px;
    }

    @include small-tablet {
      margin-top: 40px;
    }

    @include mobile {
      padding: 14px 30px;
      width: 100%;
    }
  }
}
