.reviews {
  padding-top: 110px;
  padding-bottom: 110px;
  background-color: var(--lightGrey-color);

  @media (max-width: 1130px) {
    padding-top: 90px;
    padding-bottom: 90px;
  }

  @include small-tablet {
    padding-top: 70px;
    padding-bottom: 70px;
  }

  @include mobile {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  &__heading {
    margin-bottom: 55px;

    @include medium-tablet {
      margin-bottom: 45px;
    }

    @include small-tablet {
      margin-bottom: 35px;
    }
  }

  &__descr {
    margin-bottom: 4px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 24px;
    row-gap: 50px;

    @media (max-width: 1170px) {
      column-gap: 18px;
    }

    @media (max-width: 860px) {
      grid-template-columns: repeat(1, 1fr);
      row-gap: 20px;
    }

    @include mobile {
      row-gap: 10px;
    }
  }

  &__item {
    padding: 27px 32px;
    background-color: #F8F8F8;
    transition: background-color 0.3s ease;

    @media (max-width: 1170px) {
      padding: 25px 27px;
    }

    @include tablet {
      padding: 20px 22px;
    }

    &:nth-child(n + 4) {
      display: none;
    }

    &.is-visible {
      display: block;
    }

    &:hover {
      background-color: #FFFEFE;
    }
  }

  &__item-human {
    display: flex;
    align-items: center;
    margin-bottom: 34px;

    @media (max-width: 1170px) {
      margin-bottom: 30px;
    }

    @include tablet {
      margin-bottom: 25px;
    }
  }

  &__item-img {
    min-width: 60px;
    min-height: 60px;
    margin-right: 27px;
    border-radius: 100%;
    box-shadow: (0px 8.5122px 25.5366px rgba(48, 62, 73, 0.16));

    @media (max-width: 1170px) {
      width: 70px;
      height: 70px;
      margin-right: 20px;
    }

    @include tablet {
      width: 40px;
      height: 40px;
      margin-right: 15px;
    }

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__item-info {

  }

  &__item-name {
    margin: 0;
    margin-bottom: 8px;
    font-weight: 600;
    font-size: 24px;
    line-height: 30px;
    color: var(--lightDark-color);

    @media (max-width: 1170px) {
      font-size: 20px;
      line-height: 26px;
    }

    @include tablet {
      font-size: 18px;
      line-height: 24px;
      margin-bottom: 4px;
    }
  }

  &__item-post {
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    color: var(--grey-color);

    @media (max-width: 1170px) {
      font-size: 14px;
      line-height: 18px;
    }
  }

  &__item-text {
    margin-bottom: 28px;
    font-weight: 400;
    font-size: 14px;
    line-height: 160%;
    letter-spacing: 0.03em;
    color: var(--grey-color);

    @media (max-width: 1170px) {
      margin-bottom: 20px;
    }

    @include tablet {
      margin-bottom: 15px;
    }
  }
}


