.numbers {
  padding-top: 76px;
  padding-bottom: 82px;
  background-color: var(--primary-color);

  @include tablet {
    padding-top: 60px;
    padding-bottom: 65px;
  }

  @include medium-tablet {
    padding-top: 55px;
    padding-bottom: 60px;
  }

  @include mobile {
    padding-top: 40px;
    padding-bottom: 45px;
  }

  &__list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;

    @media (max-width: 400px) {
      flex-direction: column;
    }
  }

  &__item {
    position: relative;

    @include small-tablet {
      flex: 1 1 50%;

      &:nth-child(-n+2) {
        margin-bottom: 30px;
      }
    }

    @media (max-width: 400px) {
      width: 100%;

      &:nth-child(-n+3) {
        margin-bottom: 50px;
      }
    }

    &:not(:last-child)::before {
      position: absolute;
      content: "";
      right: -66px;
      top: 0;
      width: 2px;
      height: 110px;
      background-color: var(--light-color);

      @media (max-width: 1199px) {
        display: none;
      }

      @media (max-width: 400px) {
        display: flex;
        left: 0;
        right: 0;
        top: auto;
        bottom: -30px;
        width: 100%;
        height: 1px;
        opacity: 0.3;
      }
    }
  }

  &__item-number {
    margin-bottom: 6px;
    font-family: "WorkSans", sans-serif;
    text-align: center;
    font-weight: 700;
    font-size: 45px;
    line-height: 117%;
    letter-spacing: -0.02em;
    color: var(--light-color);

    @include tablet {
      font-size: 40px;
    }

    @include medium-tablet {
      font-size: 35px;
    }
  }

  &__item-subtitle {
    font-family: "WorkSans", sans-serif;
    text-align: center;
    font-weight: 400;
    font-size: 24px;
    line-height: 117%;
    letter-spacing: -0.02em;
    color: #DCDCDC;

    @include tablet {
      font-size: 20px;
    }

    @include medium-tablet {
      font-size: 18px;
    }
  }
}
