.footer {
  padding-top: 65px;
  padding-bottom: 60px;
  background-color: #1D2225;

  @include medium-tablet {
    padding-top: 55px;
    padding-bottom: 25px;
  }

  &__top {
    display: flex;
    align-items: start;
    justify-content: space-between;
    margin-bottom: 110px;

    @media (max-width: 870px) {
      flex-direction: column;
      align-items: center;
      margin-bottom: 70px;
    }
  }

  &__info {
    max-width: 216px;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: var(--light-color);

    @media (max-width: 870px) {
      max-width: 100%;
      align-items: center;
      justify-content: center;
      text-align: center;
      margin-bottom: 30px;
    }

    &-logo {
      margin-bottom: 20px;

      @media (max-width: 870px) {
        margin-bottom: 15px;
      }
    }

    &-descr {
      margin-bottom: 15px;

      @media (max-width: 870px) {
        margin-bottom: 10px;
      }
    }

    &-address {
      margin-bottom: 10px;

      @media (max-width: 870px) {
        margin-bottom: 5px;
      }
    }
  }

  &__nav {
    display: flex;

    @media (max-width: 870px) {
      display: none;
    }

    &-column:not(:last-child) {
      margin-right: 100px;

      @include medium-tablet {
        margin-right: 50px;
      }
    }

    &-title {
      margin: 0;
      margin-bottom: 20px;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      color: var(--light-color);
    }

    &-item:not(:last-child) {
      margin-bottom: 11px;
    }

    &-link {
      font-weight: 400;
      font-size: 14px;
      line-height: 18px;
      color: var(--light-color);
      transition: color 0.3s ease;

      &:hover {
        color: var(--primary-color);
      }
    }
  }

  &__subscribe {
    width: 300px;

    @media (max-width: 870px) {
      width: 100%;
      text-align: center;
    }

    &-title {
      margin: 0;
      margin-bottom: 20px;
      font-weight: 500;
      font-size: 16px;
      line-height: 28px;
      color: var(--light-color);

      @media (max-width: 870px) {
        margin-bottom: 10px;
      }
    }

    &-descr {
      max-width: 250px;
      margin-bottom: 30px;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      color: var(--light-color);

      @media (max-width: 870px) {
        max-width: 100%;
        margin-bottom: 20px;
      }
    }
  }

  &__form {
    position: relative;
    width: 100%;
  }

  &__input {
    width: 100%;
    padding: 15px 60px 15px 15px;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    display: flex;
    align-items: center;
    color: #B8B8B8;
    border-radius: 5px;
    border: 1px solid var(--light-color);
    background: transparent;
  }

  &__btn {
    position: absolute;
    width: 30px;
    height: 30px;
    right: 15px;
    top: 50%;
    transform: translateY(-50%);
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (max-width: 870px) {
      flex-direction: column;
    }
  }

  &__copy {
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: var(--light-color);

    @media (max-width: 870px) {
      margin-bottom: 15px;
    }
  }

  &__socials {
    display: flex;
    align-items: center;
  }
}

.social__item:not(:last-child) {
  margin-right: 24px;
}

.social__link {
  svg {
    stroke: var(--light-color);
    opacity: 1;
    transition: stroke 0.3s ease;

    &:hover {
      stroke: var(--primary-color);
    }

    &:active {
      opacity: 0.7;
    }
  }
}

// 

.accordion__list {
  width: 100%;
  margin-bottom: 35px;

  @media (min-width: 870px) {
    display: none;
  }
}

.accordion__control {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px 15px 50px;
  border-bottom: solid 1px rgba(64, 68, 68, 0.4);
  width: 100%;

  background-color: transparent;
  cursor: pointer;
}

.accordion__control::-webkit-details-marker {
  display: none;
}

.accordion__content {
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  padding: 0 20px;
  will-change: max-height;
  transition: all 0.3s ease-out;
  box-sizing: content-box;
}

.accordion__title {
  text-align: center;
  margin-right: auto;
  margin-left: auto;
  font-size: 17px;
  font-weight: 700;
  line-height: 114%;
  color: var(--light-color);
}

.accordion__icon {
  position: relative;
  display: inline-block;
  width: 30px;
  height: 30px;
  border-radius: 100%;
  background-color: var(--primary-color);
}

.accordion__icon::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 24px;
  height: 24px;
  background-image: url('../img/arrow.svg');
  transform: translate(-50%, -50%);
}

.accordion__icon {
  transition: transform 0.3s ease-out;
}

.open .accordion__icon {
  transform: rotate(-90deg);
}

.open {
  .accordion__content {
    opacity: 1;
    padding: 20px;
    text-align: center;
    margin-bottom: 10px;
  }

  .accordion__title {
    color: var(--primary-color);
  }
}
