.hero {
  padding-top: 184px;
  padding-bottom: 20px;

  @include mobile {
    padding-top: 140px;
  }

  &::before {
    position: absolute;
    content: "";
    top: 73px;
    left: -114px;
    width: 228px;
    height: 227px;
    background-image: url('../img/hero/star.svg');
    background-repeat: no-repeat;
    z-index: -1;

    @media (max-width: 1370px) {
      display: none;
    }
  }

  &__container {
    display: flex;
    align-items: start;
    justify-content: space-between;

    @include medium-tablet {
      flex-direction: column;
      align-items: center;
    }

    @include mobile {
      align-items: flex-start;
    }
  }

  &__content {
    max-width: 600px;

    @media (max-width: 1130px) {
      max-width: 460px;
    }

    @include medium-tablet {
      align-items: center;
      justify-content: center;
      margin-bottom: 50px;
    }

    @include mobile {
      align-items: start;
      justify-content: flex-start;
      max-width: 350px;
      margin-bottom: 35px;
    }

    @media (max-width: 360px) {
      max-width: 300px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 100px;
    font-weight: 600;
    font-size: 96px;
    line-height: 112%;
    letter-spacing: 0.016em;
    color: var(--dark-color);

    @media (max-width: 1130px) {
      font-size: 82px;
      margin-bottom: 60px;
    }

    @include medium-tablet {
      margin-bottom: 40px;
    }

    @include mobile {
      font-size: 56px;
      margin-bottom: 30px;
    }

    @media (max-width: 360px) {
      font-size: 48px;
      margin-bottom: 20px;
    }
  }

  &__btns {
    align-items: center;
  }

  &__img {
    position: relative;
    right: -60px;

    @include medium-tablet {
      right: 0;
      left: 0;
    }

    img {
      width: 554px;
      height: 550px;
      object-fit: contain;

      @media (max-width: 1130px) {
        width: 503px;
        height: 500px;
      }

      @include mobile {
        width: 402px;
        height: 400px;
      }

      @media (max-width: 440px) {
        width: 300px;
        height: 300px;
      }
    }
  }
}

.secondary__text {
  color: var(--primary-color);
}

.decor__text {
  position: relative;
  padding-left: 148px;
  align-items: center;

  @media (max-width: 1130px) {
    padding-left: 80px;
  }

  @include mobile {
    padding-left: 65px;
  }

  @media (max-width: 360px) {
    padding-left: 50px;
  }

  &::before {
    position: absolute;
    content: "";
    top: 50%;
    left: 29px;
    transform: translateY(-50%);
    display: block;
    width: 91px;
    height: 91px;
    background-image: url('../img/hero/ellipse.svg');
    background-repeat: no-repeat;
    background-size: contain;

    @media (max-width: 1130px) {
      left: 0;
      top: 45%;
      width: 68px;
      height: 68px;
    }

    @include mobile {
      width: 46px;
      height: 46px;
    }

    @media (max-width: 360px) {
      width: 38px;
      height: 38px;
    }
  }
}
