.services {
  padding-top: 70px;
  padding-bottom: 120px;

  @media (max-width: 1130px) {
    padding-bottom: 90px;
  }

  @include small-tablet {
    padding-top: 60px;
    padding-bottom: 70px;
  }

  @include mobile {
    padding-top: 40px;
    padding-bottom: 50px;
  }

  &__heading {
    margin-bottom: 65px;

    @include medium-tablet {
      margin-bottom: 50px;
    }

    @include small-tablet {
      margin-bottom: 40px;
    }
  }

  &__title {
    margin-bottom: 13px;

    @include medium-tablet {
      margin-bottom: 5px;
    }
  }

  &__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 24px;

    @include medium-tablet {
      grid-template-columns: repeat(2, 1fr);
    }

    @include small-tablet {
      gap: 20px;
    }

    @include mobile {
      grid-template-columns: repeat(1, 1fr);
      gap: 16px;
    }
  }

  &__item {
    padding: 43px 50px 37px 50px;
    border: 2px solid #DCDCDC;
    border-radius: 16px;
    background-color: transparent;
    transition: background-color 0.3s ease, border 0.3s ease;

    @include medium-tablet {
      padding: 35px 40px 30px 40px;
    }

    @include small-tablet {
      padding: 25px 25px 20px 25px;
    }

    @include mobile {
      padding: 20px 20px 15px 20px;
    }

    &-img {
      svg {
        fill: var(--light-color);
        width: 40px;
        height: 40px;
        transition: fill 0.3s ease;

        @include small-tablet {
          width: 34px;
        }

        @include mobile {
          width: 28px;
        }
      }

      margin-bottom: 40px;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      width: 88px;
      height: 88px;
      background-color: var(--dark-color);
      border-radius: 9px;
      transition: background-color 0.3s ease, border-radius 0.3s ease;

      @include small-tablet {
        margin-bottom: 30px;
        width: 70px;
        height: 70px;
      }

      @include mobile {
        margin-bottom: 15px;
        width: 60px;
        height: 60px;
      }
    }

    &-title {
      margin: 0;
      margin-bottom: 40px;
      font-weight: 500;
      font-size: 24px;
      line-height: 125%;
      color: var(--dark-color);
      transition: color 0.3s ease;

      @include small-tablet {
        margin-bottom: 30px;
        font-size: 22px;
      }

      @include mobile {
        margin-bottom: 10px;
        font-size: 20px;
      }
    }

    &-descr {
      margin: 0;
      margin-bottom: 40px;
      font-weight: 400;
      font-size: 16px;
      line-height: 150%;
      color: var(--grey-color);
      transition: color 0.3s ease;

      @include small-tablet {
        margin-bottom: 30px;
      }

      @include mobile {
        margin-bottom: 10px;
        font-size: 14px;
      }
    }

    &-link {
      font-weight: 500;
      font-size: 16px;
      line-height: 150%;
      color: var(--dark-color);
      transition: color 0.3s ease, text-decoration 0.3s ease;

      @include mobile {
        font-size: 14px;
      }
    }
  }
}

.services__item:hover {
  border: 2px solid var(--primary-color);
  background-color: var(--primary-color);
}

.services__item:hover .services__item-img  {
    svg {
      fill: var(--dark-color);
    }

    background-color: var(--light-color);
    border-radius: 15px;
  }

.services__item:hover .services__item-title {
  color: #F9FDFF;
}

.services__item:hover .services__item-descr {
  color: #F9FDFF;
}

.services__item:hover .services__item-link {
  color: #F9FDFF;
}

.services__item-link:hover {
  text-decoration: underline;
}
