.partners {
  padding-top: 60px;
  padding-bottom: 60px;

  @include medium-tablet {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  @include mobile {
    padding-top: 25px;
    padding-bottom: 25px;
  }

  &__logos-list {
    display: flex;
    align-items: end;
    justify-content: space-between;
    flex-wrap: wrap;

    @include mobile {
      justify-content: center;
      column-gap: 30px;
      row-gap: 10px;
    }
  }
}

.logo__item {

  svg {
    fill: #60658C;
    opacity: 0.5;
    transition: opacity 0.3s ease, fill 0.3s ease;

    @media (max-width: 900px) {
      max-width: 150px;
    }

    @include small-tablet {
      max-width: 100px;
    }

    @include mobile {
      max-width: 120px;
    }

    &:hover {
      opacity: 1;
    }

    &:active {
      fill: var(--primary-color);
    }
  }
}
