.submit {
  padding-top: 100px;
  padding-bottom: 100px;

  @media (max-width: 1130px) {
    padding-top: 90px;
    padding-bottom: 90px;
  }

  @include small-tablet {
    padding-top: 70px;
    padding-bottom: 70px;
  }

  @include mobile {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  &__container {
    display: flex;
    align-items: start;
    justify-content: space-between;

    @include medium-tablet {
      flex-direction: column-reverse;
      align-items: center;
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
    flex: 0 0 588px;

    padding: 40px;
    border-radius: 16px;
    background-color: var(--light-color);

    @include medium-tablet {
      border: 1px solid #EBEBEB;
    }

    @include small-tablet {
      flex: 1 1 100%;
      padding: 30px;
    }

    @include mobile {
      padding: 25px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 50px;
    font-weight: 600;
    font-size: 36px;
    line-height: 100%;
    color: var(--lightDark-color);

    @include small-tablet {
      margin-bottom: 35px;
      font-size: 32px;
    }

    @include mobile {
      margin-bottom: 25px;
      font-size: 26px;
    }
  }

  &__img {
    padding-top: 35px;
  }
}

.form__input-box {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  margin-bottom: 40px;

  @include small-tablet {
    margin-bottom: 30px;
  }

  @include mobile {
    max-width: 100%;
    margin-bottom: 25px;
  }
}

.form__textarea {
  max-width: 100%;
  margin-bottom: 30px;

  @include small-tablet {
    margin-bottom: 20px;
  }
}

.form__label {
  max-width: 214px;

  @include mobile {
    max-width: none;
    width: 100%;

    &:not(:last-child) {
      margin-bottom: 25px;
    }
  }

  &:not(:last-child) {
    margin-right: 80px;

    @include small-tablet {
      margin-right: 50px;
    }

    @include mobile {
      margin-right: 0;
    }
  }
}

.form__input-title {
  margin-bottom: 16px;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: var(--lightDark-color);

  @include small-tablet {
    margin-bottom: 10px;
    font-size: 13px;
    line-height: 16px;
  }

  @include mobile {
    width: 100%;
  }
}

.form__input {
  padding: 10px 16px;
  border-radius: 12px;
  background-color: var(--lightGrey-color);

  @include mobile {
    width: 100%;
  }

  &--textarea {
    border: none;
    box-shadow: none;
    width: 100%;
    height: 130px;
    min-height: 50px;
    max-width: 508px;
  }

  &::placeholder {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: #A6A6A6;

    @include small-tablet {
      font-size: 14px;
    }
  }
}

.btn-submit {
  width: 100%;
  margin-right: 0;
}
