.why-us {
  position: relative;
  padding-top: 100px;
  padding-bottom: 130px;

  @media (max-width: 1130px) {
    padding-top: 90px;
    padding-bottom: 90px;
  }

  @include small-tablet {
    padding-top: 70px;
    padding-bottom: 70px;
  }

  @include mobile {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  &::before {
    position: absolute;
    content: "";
    left: 0;
    top: 0;
    width: 100%;
    height: 743px;
    background-color: var(--primary-color);
    z-index: -1;

    @include medium-tablet {
      height: 650px;
    }

    @include small-tablet {
      height: 480px;
    }

    @include mobile {
      height: 300px;
    }
  }

  &__heading {
    margin-bottom: 110px;

    @media (max-width: 1130px) {
      margin-bottom: 70px;
    }

    @include small-tablet {
      margin-bottom: 50px;
    }

    @include mobile {
      margin-bottom: 40px;
    }
  }

  &__descr {
    margin-bottom: 4px;
    text-align: left;
    color: var(--lightGrey-color);
  }

  &__title {
    text-align: left;
    color: var(--light-color);
  }

  &__video {
    position: relative;
    width: 100%;
    height: 640px;
    border-radius: 63px;
    margin-bottom: 95px;

    @media (max-width: 1130px) {
      margin-bottom: 70px;
    }

    @include medium-tablet {
      height: 550px;
    }

    @include small-tablet {
      height: 380px;
      margin-bottom: 60px;
    }

    @include mobile {
      height: 320px;
      margin-bottom: 40px;
    }

    @media (max-width: 400px) {
      height: 220px;
    }

    @media (max-width: 360px) {
      height: 180px;
    }

    video {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 63px;

      @include medium-tablet {
        border-radius: 50px;
      }

      @include small-tablet {
        border-radius: 40px;
      }
    }
  }

  &__play {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 150px;
    height: 150px;
    transform: translate(-50%, -50%);

    @include medium-tablet {
      width: 130px;
      height: 130px;
    }

    @include small-tablet {
      width: 100px;
      height: 100px;
    }

    @include mobile {
      width: 80px;
      height: 80px;
    }

    svg {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__play--hidden {
    display: none;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 100px;
    row-gap: 55px;

    @media (max-width: 1130px) {
      column-gap: 70px;
    }

    @include tablet {
      column-gap: 50px;
      row-gap: 45px;
    }

    @include medium-tablet {
      column-gap: 40px;
      row-gap: 35px;
    }

    @media (max-width: 620px) {
      grid-template-columns: repeat(1, 1fr);
    }

    @include mobile {
      row-gap: 25px;
    }
  }

  &__item {
    display: flex;
    align-items: start;

    &-img {
      display: block;
      min-width: 40px;
      min-height: 40px;
      margin-right: 20px;

      @include mobile {
        min-width: 30px;
        min-height: 30px;
        margin-right: 12px;
      }

      img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    &-content {
      margin-top: 7px;

      @include mobile {
        margin-top: 2px;
      }
    }

    &-title {
      margin: 0;
      margin-bottom: 20px;
      font-weight: 600;
      font-size: 24px;
      line-height: 30px;
      color: #1D2225;

      @include mobile {
        font-size: 20px;
        margin-bottom: 15px;
      }
    }

    &-descr {
      margin: 0;
      font-weight: 400;
      font-size: 16px;
      line-height: 160%;
      letter-spacing: 0.016em;
      color: var(--grey-color);

      @include mobile {
        font-size: 14px;
      }
    }
  }
}
