.header {
  position: absolute;
  left: 0;
  top: 0;
  padding: 21px 0;
  width: 100%;

  @include medium-tablet {
    padding: 17px 0;
  }
}

.logo {
  img {
    width: 100%;
    height: 100%;

    @include small-tablet {
      width: 90%;
      height: 90%;
    }
  }
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nav {
  display: flex;
  align-items: center;

  @include small-tablet {
    position: fixed;
    right: 0;
    top: 0;
    z-index: 500;
    overflow-y: auto;
    width: 80%;
    height: 100vh;
    flex-direction: column;
    justify-content: center;
    background-color: var(--light-color);
    box-shadow: -50px 0px 200px rgba(29, 34, 37, 0.4);
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }
}

.nav__list {
  display: flex;
  align-items: center;
  margin-right: 100px;

  @include medium-tablet {
    margin-right: 55px;
  }

  @include small-tablet {
    flex-direction: column;
    margin-right: 0;
    margin-bottom: 50px;
  }
}

.nav__item {
  &:not(:last-child) {
    margin-right: 80px;

    @include medium-tablet {
      margin-right: 45px;
    }

    @include small-tablet {
      margin-right: 0;
      margin-bottom: 35px;
    }
  }
}

.nav__link {
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: #546C80;
  transition: color 0.3s ease;

  &:hover {
    color: var(--primary-color);
  }

  &:active {
    color: var(--lightDark-color);
  }

  @include small-tablet {
    font-size: 18px;
  }
}

.header__btn {
  display: inline-flex;
  padding: 15px 30px;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: var(--light-color);
  border-radius: 10px;
  background-color: #282A2C;
  transition: background-color 0.3s ease, color 0.3s ease;

  &:hover {
    background-color: var(--primary-color);
  }

  &:active {
    color: var(--light-color);
    background-color: var(--darkPrimary-color);
  }
}

.burger {
  @include burger;
  z-index: 501;

  @media (min-width: 769px) {
    display: none;
  }
}

.menu--active {
  opacity: 1;
  visibility: visible;
}


