.title {
  margin: 0;
  font-weight: 600;
  font-size: 56px;
  line-height: 125%;
  text-align: center;
  color: var(--dark-color);

  @media (max-width: 1130px) {
    font-size: 52px;
  }

  @include medium-tablet {
    font-size: 46px;
  }

  @include small-tablet {
    font-size: 38px;
  }

  @include mobile {
    font-size: 28px;
  }


}

.descr {
  margin: 0;
  font-weight: 400;
  font-size: 24px;
  line-height: 112%;
  text-align: center;
  color: var(--lightDark-color);

  @media (max-width: 1130px) {
    font-size: 22px;
  }

  @include medium-tablet {
    font-size: 20px;
  }

  @include small-tablet {
    font-size: 18px;
  }

  @include mobile {
    font-size: 16px;
  }
}

